import {
  Backdrop,
  Box,
  Button,
  Fade,
  Modal,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { useState } from "react";

function createData(name: string, rank: number, explain: string) {
  return { name, rank, explain };
}

const rows = [
  createData("", 1, ""),
  createData("", 2, ""),
  createData("", 3, ""),
  createData("", 4, ""),
  createData("", 5, ""),
  createData("", 6, ""),
  createData("", 7, ""),
  createData("", 8, ""),
  createData("", 9, ""),
  createData("", 10, ""),
  createData("", 11, ""),
  createData("", 12, ""),
  createData("", 13, ""),
  createData("", 14, ""),
  createData("", 15, ""),
];

const styles = {
  modal: {
    position: "absolute" as "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: "60%",
    bgcolor: "background.paper",
    border: "2px solid #000",
    boxShadow: 24,
    p: 4,
    pb: 0,
    maxHeight: "80vh",
    overflow: "scroll",
  },
};

export const RuleModal = () => {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Box sx={{ mt: 2 }}>
        <Button
          onClick={handleOpen}
          variant="outlined"
          sx={{ width: 160, height: 30, fontSize: "15px" }}
        >
          
        </Button>
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          open={open}
          onClose={handleClose}
          closeAfterTransition
          slots={{ backdrop: Backdrop }}
          slotProps={{
            backdrop: {
              timeout: 500,
            },
          }}
        >
          <Fade in={open}>
            <Box sx={styles.modal}>
              <Typography
                id="transition-modal-title"
                variant="h6"
                component="h2"
              >
                
              </Typography>
              <Typography id="transition-modal-description" sx={{ mt: 2 }}>
                
              </Typography>
              <Typography id="transition-modal-description">
                
              </Typography>
              <Typography id="transition-modal-description">
                
              </Typography>
              <Typography id="transition-modal-description">
                
              </Typography>
              <Typography id="transition-modal-description">
                
              </Typography>
              <Typography id="transition-modal-description">
                
              </Typography>
              <Typography id="transition-modal-description" sx={{ mt: 2 }}>
                
              </Typography>
              <TableContainer component={Paper} sx={{ mt: 2 }}>
                <Table aria-label="simple table">
                  <TableHead>
                    <TableRow>
                      <TableCell></TableCell>
                      <TableCell align="right"></TableCell>
                      <TableCell align="right"></TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {rows.map((row) => (
                      <TableRow
                        key={row.name}
                        sx={{
                          "&:last-child td, &:last-child th": { border: 0 },
                        }}
                      >
                        <TableCell component="th" scope="row">
                          {row.name}
                        </TableCell>
                        <TableCell align="right">{row.rank}</TableCell>
                        <TableCell align="right">{row.explain}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  mt: 1,
                  p: 2,
                  position: "sticky",
                  bottom: 0,
                  background: "white",
                }}
              >
                <Button
                  onClick={handleClose}
                  variant="outlined"
                  sx={{
                    width: 100,
                    height: 30,
                    fontSize: "15px",
                    p: 2,
                  }}
                >
                  
                </Button>
              </Box>
            </Box>
          </Fade>
        </Modal>
      </Box>
    </div>
  );
};
